.c-tableSorter {
  table-layout: fixed;
}
.c-tableSorter__status {
  min-height: 30px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  padding-left: 0.5rem;
}
.c-tableSorter__subcell {
  display: flex;
  padding: 0.25rem 0.5rem 0.25rem 0.5rem;
}
.c-tableSorter .checkbox {
  width: 70px;
}
.c-tableSorter .header {
  cursor: pointer;
}
.c-tableSorter .headerSortDesc {
  background: #0067C5;
  color: white;
}
.c-tableSorter .headerSortDesc p:after {
  content: " ▲";
}
.c-tableSorter .headerSortAsc {
  background: #0067C5;
  color: white;
}
.c-tableSorter .headerSortAsc p:after {
  content: " ▼";
}
.c-tableSorter__sort-input {
  margin-bottom: 0px !important;
}
.c-tableSorter__sort-input .skjemaelement__label {
  display: none;
}
.c-tableSorter__checkbox {
  margin-bottom: 0px !important;
}
.c-tableSorter__checkbox .skjemaelement__label {
  display: block !important;
}
.c-tableSorter__table {
  width: 100%;
}
.c-tableSorter__table thead {
  border-bottom: 2px solid grey;
}
.c-tableSorter__table thead th {
  padding: 0.5rem;
  text-align: center;
  cursor: pointer;
}
.c-tableSorter__table thead th:hover {
  background-color: #005B82;
  color: white;
}
.c-tableSorter__table thead th:nth-child(1) {
  width: 5%;
  display: block;
}
.c-tableSorter__table tbody tr {
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  border-bottom: 1px solid lightgrey;
}
.c-tableSorter__table td {
  padding: 0.25rem 0.5rem;
  overflow-wrap: break-word;
}
.c-tableSorter__table td input {
  width: 100%;
}
.c-tableSorter__table td:first-child {
  width: 50px;
  display: block;
}
